
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
    name: 'cash',
    components: {
        PopupPicker, Cell, Group, XInput, XButton, XHeader, ButtonTab, ButtonTabItem
    },
    data (){
        return {
            bankList:[['中国银行',
                '华夏银行',
                '民生银行',
                '招商银行']],
            form:{
                // 提现方式，0提现至个人帐号，1提现至公司账户
                type: 0,
                bankName: [],
                cardNo:'',
                name:'',
                vcode:''
            },
            number: 60,
            showNumber: false
        }
    },
    methods: {
        // 表单验证
        formCheck (){
            let form = this.form;
            if(!form.bankName.length){
                this.showInfo('请选择开户银行');
            }else if(!form.cardNo){
                this.showInfo('请填写银行卡号')
            }else if(!form.name){
                switch (form.type){
                    case 0:
                        this.showInfo('请填写开户人姓名');
                        break;
                    default:
                        this.showInfo('请填写公司名称');
                        break
                }
            }else if(!form.vcode){
                this.showInfo('请填写验证码')
            }else {
                return true
            }
            return false

        },
        // 获取验证码
        handlerClickVcode () {
            this.showNumber = true;
            let self = this;
            let interval = setInterval(function () {
                if(self.number > 0){
                    self.number --
                }else {
                    clearInterval(interval)
                }
            },1000)
        },
        handlerSubmit (){
            if(this.formCheck()){
                console.log('提交表单');
            }
        }
    }
}



